/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700&display=swap');

/* Existing styles */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Add custom font classes */
.font-raleway {
  font-family: 'Raleway', sans-serif;
}

.font-opensans {
  font-family: 'Open Sans', sans-serif;
}

@layer base {
  body {
    @apply bg-white dark:bg-[#121212];
    @apply text-gray-900 dark:text-gray-100;
  }
}

/* Dark mode system preferences */
@media (prefers-color-scheme: dark) {
  body {
    color-scheme: dark;
  }
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  @apply border-gray-200 dark:border-dark-border;
}

th {
  @apply bg-gray-100 dark:bg-dark-section;
  @apply text-gray-900 dark:text-gray-200;
}

.menu-table tr:nth-child(even) {
  @apply bg-gray-50 dark:bg-dark-section;
}

.menu-table tr:hover {
  @apply bg-gray-100 dark:bg-dark-hover;
} 